body {
  margin: 0;
  line-height: normal;
}

.popup-overlay {
  display: flex;
  flex-direction: column;
  position: fixed;
  inset: 0;
}

:root {
  /* fonts */
  --font-inter: Inter;
  --font-navigo: Navigo;

  /* font sizes */
  --font-size-base: 1rem;
  --font-size-sm: 0.88rem;
  --font-size-lg: 1.13rem;
  --font-size-16xl: 2.19rem;
  --font-size-6xl: 1.56rem;

  /* Colors */
  --color-black: #000;
  --color-darkslategray-100: #474747;
  --color-darkslategray-200: #313131;
  --color-white: #fff;
  --color-gray-300: #2c2c2c;
  --color-gray-500: #2b2b2b;
  --color-gray-200: #1e1e1e;
  --color-gray: #121212;

  /* Gaps */
  --gap-6xl: 1.56rem;
  --gap-21xl: 2.5rem;
  --gap-11xl: 1.88rem;
  --gap-3xs: 0.63rem;
  --gap-xl: 1.25rem;
  --gap-mini: 0.94rem;
  --gap-6xs: 0.44rem;
  --gap-31xl: 3.13rem;
  --gap-81xl: 6.25rem;

  /* Paddings */
  --padding-xl: 1.25rem;
  --padding-3xs: 0.63rem;
  --padding-21xl: 2.5rem;
  --padding-xs: 0.75rem;
  --padding-61xl: 5rem;
  --padding-231xl: 15.63rem;
  --padding-31xl: 3.13rem;
  --padding-6xl: 1.56rem;
  --padding-base: 1rem;
  --padding-15xl: 2.13rem;
  --padding-2xs: 0.69rem;
  --padding-12xs: 0.06rem;

  /* border radiuses */
  --br-3xs: 10px;
  --br-mini: 15px;
  --br-xl: 20px;
  --br-8xs: 5px;
  --br-9xs: 4px;
}
